  height: 50px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 20px;
  padding: 5px;
  font-size: 20px;
}

input {
  height: 40px;
}

.loader {
  color: #555;
  padding: 10px;
  font-size: 24px;
  display: none;
}

.error {
  color: red;
  padding: 10px;
  font-size: 24px;
  display: block;
}

.cat-info {
  color: #000;
  background-color: #fff;
  padding: 20px;
  font-size: 19px;
}

.cat-info img, .cats-list img, .random-cats-list img {
  max-width: 1200px;
  --s: 18px;
  --b: 1px;
  --w: 500px;
  --c: #668284;
  aspect-ratio: 1;
  object-fit: cover;
  padding: calc(2 * var(--s) );
  --_g: var(--c) var(--b), rgba(0, 0, 0, 0) 0 calc(100% - var(--b) ), var(--c) 0;
  background: linear-gradient(var(--_g) ) 50% / 100% var(--_i, 100%) no-repeat, linear-gradient(90deg, var(--_g) ) 50% / var(--_i, 100%) 100% no-repeat;
  outline: calc(var(--w) / 2) solid rgba(0, 0, 0, .004);
  outline-offset: calc(var(--w) / -2 - 2 * var(--s) );
  cursor: pointer;
  transition: all .4s;
}

.cat-info img:hover, .cats-list img:hover, .random-cats-list img:hover {
  outline: var(--b) solid var(--c);
  outline-offset: calc(var(--s) / -2);
  --_i: calc(100% - 2 * var(--s) );
}

.cats-list, .random-cats-list {
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
  display: flex;
}

.cat-item {
  margin: 20px;
  display: flex;
}

.cat-item img {
